import React from 'react';
import { NavLink } from 'react-router-dom';


const Navbar = () =>{

    return(
    <>

<nav className="navbar navbar-expand-lg navbar-light bg-light">
  <div className="container-fluid">

    <NavLink  className="navbar-brand " to="/">MR.SHAHIDTECH</NavLink>

    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>

    <div className="collapse navbar-collapse " id="navbarSupportedContent">

      <ul className="navbar-nav  me-auto mb-2 mb-lg-0 m-lg-auto ">

        <li className="nav-item ">
          <NavLink activeClassName="active-menu" className="nav-link active" aria-current="page" exact to="/">Home</NavLink>
        </li>

        <li className="nav-item">
          <NavLink activeClassName="active-menu" className="nav-link active " aria-current="page" exact to="/Service">Service</NavLink>
        </li>

        <li className="nav-item">
          <NavLink activeClassName="active-menu" className="nav-link active" aria-current="page" exact to="/About">About</NavLink>
        </li>

        <li className="nav-item">
          <NavLink activeClassName="active-menu" className="nav-link active" aria-current="page" exact to="/Contact">Contact</NavLink>
        </li>
       
        
      </ul>
      <form className="d-flex">
        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
        <button className="btn btn-outline-success" type="submit">Search</button>
      </form>
    </div>
  </div>
</nav>
       
    </>    
        
    )
}

export default Navbar;